/* Font imports */
@import url('https://fonts.googleapis.com/css2?family=Karla:wght@400;700&family=Poppins&display=swap');

/* styles.css */
  /* Create a custom scrollbar for Webkit browsers */
  /* Adjust styles according to your design */
  ::-webkit-scrollbar {
    width: 8px;
  }
  
  ::-webkit-scrollbar-thumb {
    background-color: #D2C2A4;
    border-radius: 8px;
  }
  
  ::-webkit-scrollbar-track {
    background-color: #FFF9E4;
  }
  



/* Text drop shadow */

.nav-text-shadow {
    text-shadow: 2px 2px 2px rgba(9, 14, 58, 0.30);
}